.container-items{
    display: flex;
    flex-direction: column;
    gap: 30px;
    border-top: black 1px solid;
    width: 300px;
    padding: 15px;
    margin-top: 30px;
    }
.container-skin-list, .container-shirt-list {
    display        : flex;
    justify-content: center;
    gap            : 15px;
    
}

.container-skin-list .skin, .container-shirt-list .shirt {
    border-radius: 50%;

    width     : 50px;
    height    : 50px;
    box-shadow: 3px 3px 3px #bebebe, -3px -3px 3px #f5f4f4;
}