h3 {
    text-align : center;
    font-weight: normal;
    font-size  : 18px;
}

.container-avatar {
    display        : flex;
    justify-content: center;
    gap            : 35px;
    margin-inline: 10px;
}

.container-avatar .avatar {
    border-radius: 50%;
    
    width     : 140px;
    height    : 140px;
    box-shadow: 3px 3px 3px #bebebe, -3px -3px 3px #f5f4f4;
}